	module.exports = {"data":{"markdownRemark":{"html":"<h3>Transparency Trust</h3>\n<p>Bug bounty programs benefit from transparency by building trust between you and your hackers. Having a clear policy page, friendly language, clear engagement rules, and benefits (like visible bounty table) are some of the simple methods you can use to break down walls and start building a trusting relationship.</p>\n<h3>Engagement</h3>\n<p>A clear bounty structure is something hackers call out when looking for programs to participate in. They want to know what they'll be rewarded fairly if they spend their time and effort helping a program. Programs with clear rewards structures tend to generate more positive engagement.</p>\n<h3>Your Teams Workload Management</h3>\n<p>Having a defined amount per severity award means your teams can make faster decisions instead of worry about how to calculate fair awards. It also precludes hackers negotiating for higher awards and back and forth messaging requesting more understanding.</p>\n<h3>Expectation Setting</h3>\n<p>This one's important and probably the primary reason we encourage a clearly defined bounty structure. History has taught us that if expectations are misaligned someone is going to get frustrated. Case in point, a bounty ranging from $1000 - $3000 for a critical vulnerability sets the expectation that I may get $1000, $2000 or even $3000! In reality, the expectation is based on the severity of the vulnerability, which can be an objective international standard (see <a href=\"https://docs.hackerone.com/programs/severity.html\">CVSS Classification</a>) or unique calculation based on the risk appetite of a business and how they define impact severity. In both cases, a subjective review is required, and this is where the expectation mismatch happens.\nHackers already have three obstacles to overcome each with its expectation barrier before getting an award. These include:\n</p>\n<ol>\n<li>Is the report valid? </li>\n<li>Is the report a critical severity vulnerability (or the same severity I class it)? </li>\n<li>Am I the first to find this bug?</li>\n<li>Will I get $1000, $2000 or $3000? </li>\n</ol>\n<p>If one of these barriers is misaligned (they often are) it sparks frustration, debate, negotiation and in some cases public outbursts leading to mediation and increasing hacker disengagement and workload on your teams and HackerOne. </p>\n<p>By setting a level expectation, we also protect the \"Bug Bounty Brand\" and mitigate risk for you and your customers allowing you to reap the rewards of Bug Bounty efforts.</p>","frontmatter":{"path":"/programs/why-are-bounty-tables-important.html","id":"programs/why-are-bounty-tables-important","title":"Why are Bounty Tables Important","description":null}}},"pathContext":{}}

